@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{height:100vh;text-align:center;background-image:repeating-linear-gradient(#0b1800 20px, #000000 25px)}.main-btn{color:#fff;font-size:large;font-family:"Righteous","cursive";padding:10px;margin:16px;border:2px solid #fff;border-radius:6px;background-image:linear-gradient(#00945c, #00746b)}.main-btn:hover{cursor:pointer}.main-btn:focus{outline:none}
.steps-bar{width:100%;height:6em;display:flex;box-shadow:0 10px 10px 1px rgba(0,0,0,.336);justify-content:stretch;background-image:linear-gradient(#0f2c2a, black);color:#fff}.steps-bar .step{flex:1 1;border:2px solid #000}.steps-bar .active-step{background-image:linear-gradient(#00746B, #0f2c2a)}.steps-bar h2{font-family:"Righteous",cursive}
.main-container{display:flex;flex-direction:column;align-items:center}.main-step-btn{border-radius:50%;margin-top:40px;height:300px;width:300px;outline:none;border:none}.main-step-btn:hover{cursor:pointer}.main-step-btn .step-btn-img{height:80%}.btn-action-text{font-family:"Righteous",cursive;color:#fff}.btn-action-subtext{color:#fff}
.match{display:flex;flex:1 1}.match .local,.match .spotify{flex-basis:45%;border-radius:10px;background-image:linear-gradient(#031110, #081413);padding:0 20px;margin:2px;color:#fff}.match .local p,.match .spotify p{flex:1 1;text-align:left;margin:.2rem;font-size:12px}.match .local .artist,.match .spotify .artist{font-size:14px;font-weight:600}.match .spotify{display:flex;justify-content:space-between;align-items:center}.match .spotify div{flex:1 1}.match .spotify img{height:75px;width:75px}.match .add-to-lib-div{width:80px;border-radius:10px;border:4px solid #001311;color:#fff;display:flex;justify-content:center;align-items:center;font-family:"Righteous","cursive"}.match .add-to-lib-div:hover{cursor:pointer}.match .add-to-lib-true{background-image:linear-gradient(#02c102, #002215)}.match .add-to-lib-false{background-image:linear-gradient(#c10202, #220000)}
.results-container{width:100vw;height:100%;display:flex;flex-direction:column;align-items:center}.grid-headers{width:100%;display:flex;color:#fff}.grid-headers>*{margin:0}.grid-headers h3{margin:0 10px;flex-basis:45%;text-align:left}.grid-headers h4{font-size:14px}.results-analysis{width:100%;box-sizing:border-box;background-color:#1ed760;margin-top:4px;margin-bottom:12px;padding:2px}.results-analysis>p{font-family:"Righteous",cursive;color:#000;display:inline}.match-grid{width:100%;overflow:hidden;overflow-y:scroll;flex:1 1}::-webkit-scrollbar{width:.5em;margin-left:4px}::-webkit-scrollbar-thumb{background:#1ed760}::-webkit-scrollbar-thumb:hover{background:#fff}::-webkit-scrollbar-track{background:#000}
.final-page-container{height:100%;display:flex;flex-direction:column;width:100%;justify-content:center;align-items:center}.final{border:2px solid #fff;border-radius:10px;box-shadow:0 0 10px 10px #000;background:#00746b;color:#fff;font-family:"Righteous","cursive";padding:20px 100px;display:flex;flex-direction:column;justify-content:center;align-items:center}.final h1,.final h4{margin:5px;font-weight:400}.final .final-form{margin-top:20px}.final .final-form .checkbox{display:none}.final .final-form .custom-checkbox{height:1em;width:1em;border-radius:50%;border:2px solid #fff;box-shadow:0 0 3px 1px inset #fafafa;background:#ec4141}.final .final-form .checkbox-true{box-shadow:0 0 3px 1px inset #fafafa;background:#00990d}.final .final-form fieldset{padding:2px;width:100%;border:none;display:flex;justify-content:space-between}.final .final-form div{margin:5px 0;padding:5px}.final .final-form .extended-field{border:2px solid #fff}
